* {
  font-family: "Times New Roman", Times, serif;
  color: white;
  margin: 0;
  font-weight: 100;
}

div#main {
  text-align: center;
  margin: 0;
  overflow: hidden;
}

div.section {
  background-image: linear-gradient(
    45deg,
    rgba(0, 0, 0, 0.25),
    rgba(0, 0, 255, 0.5),
    rgba(128, 0, 128, 0.75),
    black
  );
  width: 80vw;
  height: 80vh;
  margin-top: 10vh;
  margin-bottom: 10vh;
  position: relative;
  z-index: 100;
  display: inline-block;
  border-radius: 5vh;
  border: 3px solid rgba(255, 255, 255, 0.5);
}
@media screen and (min-width: 1200px) {
  h1 {
    font-size: 5vh;
  }
  p {
    font-size: 1.5vh;
  }
  h4 {
    font-size: 2vh;
  }
  h3 {
    font-size: 2.5vh;
  }
}
@media screen and (max-width: 1200px) {
  h1 {
    font-size: 5vh;
  }
  p {
    font-size: 1.5vh;
  }
  h4 {
    font-size: 2vh;
  }
  h3 {
    font-size: 2.5vh;
  }
}
