@media screen and (min-width: 1200px) {
    div#intro div#Name{
        height: 10vh;
        margin: 0px;
        position: relative;
        z-index: 100;
        display: inline-block;
        border-radius: 5vh;
        border: 3px solid rgba(255, 255, 255, 0.5);
        margin-bottom: 2vh;
    }
    
    div#intro div#Work {
        margin: 0px;
        margin-top: 0;
        height: 70vh;
        width: 37vw;
        position: relative;
        z-index: 100;
        display: inline-block;
        border-radius: 5vh;
        border: 3px solid rgba(255, 255, 255, 0.5);
        margin-right: 2vw;
        margin-bottom: 10vh;
    }
    
    div#intro div#Hobby {
        margin: 0px;
        margin-top: 0;
        height: 70vh;
        width: 37vw;
        position: relative;
        z-index: 100;
        display: inline-block;
        border-radius: 5vh;
        border: 3px solid rgba(255, 255, 255, 0.5);
        margin-left: 2vw;
        margin-bottom: 10vh;
    }
    
    div#intro.section {
        background-image: none;
        border: none;
    }
    
    div#intro h3,div#intro h4{
        margin-top: 3vh;
    }
}
